        @content;
    }
}
@mixin size-xxs($n){
    @media screen and (max-width: $break_xxs){
        $value: $atom * $n;
        width: #{calc(#{$value} - #{$offset})};
    }
}
@mixin col-xxs-1(){
    @include size-xxs(1);
}
